import dva from 'dva';
import { Component } from 'react';
import createLoading from 'dva-loading';
import history from '@tmp/history';

let app = null;

export function _onCreate() {
  const plugins = require('umi/_runtimePlugin');
  const runtimeDva = plugins.mergeConfig('dva');
  app = dva({
    history,
    
    ...(runtimeDva.config || {}),
    ...(window.g_useSSR ? { initialState: window.g_initialData } : {}),
  });
  
  app.use(createLoading());
  (runtimeDva.plugins || []).forEach(plugin => {
    app.use(plugin);
  });
  
  app.model({ namespace: 'auth', ...(require('C:/laragon/www/gl/resources/umi/src/models/auth.ts').default) });
app.model({ namespace: 'cards', ...(require('C:/laragon/www/gl/resources/umi/src/models/cards.ts').default) });
app.model({ namespace: 'companies', ...(require('C:/laragon/www/gl/resources/umi/src/models/companies.ts').default) });
app.model({ namespace: 'events', ...(require('C:/laragon/www/gl/resources/umi/src/models/events.ts').default) });
app.model({ namespace: 'global', ...(require('C:/laragon/www/gl/resources/umi/src/models/global.ts').default) });
app.model({ namespace: 'phases', ...(require('C:/laragon/www/gl/resources/umi/src/models/phases.ts').default) });
app.model({ namespace: 'pipes', ...(require('C:/laragon/www/gl/resources/umi/src/models/pipes.ts').default) });
app.model({ namespace: 'processes', ...(require('C:/laragon/www/gl/resources/umi/src/models/processes.ts').default) });
app.model({ namespace: 'reports', ...(require('C:/laragon/www/gl/resources/umi/src/models/reports.ts').default) });
app.model({ namespace: 'setting', ...(require('C:/laragon/www/gl/resources/umi/src/models/setting.ts').default) });
app.model({ namespace: 'tasks', ...(require('C:/laragon/www/gl/resources/umi/src/models/tasks.ts').default) });
app.model({ namespace: 'user', ...(require('C:/laragon/www/gl/resources/umi/src/models/user.ts').default) });
app.model({ namespace: 'users', ...(require('C:/laragon/www/gl/resources/umi/src/models/users.ts').default) });
  return app;
}

export function getApp() {
  return app;
}

export class _DvaContainer extends Component {
  render() {
    const app = getApp();
    app.router(() => this.props.children);
    return app.start()();
  }
}
